<template>
    <div id="container">
		
	</div>
	
</template>

<script>
import * as THREE from 'three';
import FBXLoader from 'three-fbxloader-offical'
var OrbitControls = require('three-orbit-controls')(THREE)

export default {
  name: 'ThreeScene',
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      cube: null,
	  controls: null,
	  model: null,
    }
  },
  methods: {
    init: function() {
        let container = document.getElementById('container');

        this.camera = new THREE.PerspectiveCamera(70, window.innerWidth/window.innerHeight, 0.01, 6000);
        this.camera.position.z = 10;
		this.camera.position.y = 10;
		// this.camera.rotation.x = -20 * Math.PI / 180;
		this.controls = new OrbitControls(this.camera, container);

        this.scene = new THREE.Scene();
		this.scene.background = new THREE.Color( 0xffffff);

        let geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2);
        let material = new THREE.MeshLambertMaterial( { color: 0x00ff00 } );

        this.cube = new THREE.Mesh(geometry, material);
		var spotLight = new THREE.SpotLight( 0xffffff );
		spotLight.position.set( 50, 80, 50 );

		spotLight.castShadow = true;

		spotLight.shadow.mapSize.width = 1024;
		spotLight.shadow.mapSize.height = 1024;

		spotLight.shadow.camera.near = 500;
		spotLight.shadow.camera.far = 4000;
		spotLight.shadow.camera.fov = 30;

		this.scene.add( spotLight );
		var spotLightHelper = new THREE.SpotLightHelper( spotLight );
		this.scene.add( spotLightHelper );
		var light = new THREE.AmbientLight( 0x404040 ); // soft white light
		this.scene.add( light )
        // this.scene.add(this.cube);

		var loader = new FBXLoader();

		loader.load('../assets/Models/pinetree.fbx', 
			(model) => {
				console.log(model);
				this.scene.add(model);
				model.position.set(0,0,0);
				var scale = 1 / 1000
				model.scale.set(scale,scale,scale);
			}
		);

        this.renderer = new THREE.WebGLRenderer({antialias: true, aplha: true});
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        container.appendChild(this.renderer.domElement);

    },
    animate: function() {
        requestAnimationFrame(this.animate);
        this.cube.rotation.y += 0.02;
		this.controls.update();
        this.renderer.render(this.scene, this.camera);
    }
  },
  mounted() {
      this.init();
      this.animate();
  }
}
</script>

<style lang="scss" scoped>
#container{
	clear:both;
}
</style>


